@import url(https://fonts.googleapis.com/css2?family=Space+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;font-family:"Space Mono",sans-serif}.App #quote-box{padding:40px;background-color:white;border-radius:10px;color:black;width:50%}.App #quote-box #author{text-align:end;padding-right:5%;padding-bottom:20px}.App #quote-box #tweet-quote{float:left;padding-left:5%;font-size:3.2rem;color:green}.App #quote-box #facebook-quote{float:left;padding-left:1.8%;font-size:3.2rem;color:green}.App #quote-box #new-quote{float:right;margin-right:5%;color:white;border-radius:5px;padding:10px 20px;border:none;cursor:pointer;font-family:"Space Mono",sans-serif;font-size:1.2rem}.App .App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}

